<template>
  <div class="goods-add">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item to="/home"></el-breadcrumb-item>
      <el-breadcrumb-item to="/goods"></el-breadcrumb-item>
      <el-breadcrumb-item to="/goods"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card style="margin-top: 20px;">
      <el-alert title="" type="info" show-icon :closable="false"> </el-alert>

      <el-row style="margin: 20px 0;">
        <el-col>
          <!-- finish-status:  -->
          <!-- process-status:  -->
          <el-steps :active="currentIndex" align-center finish-status="success" :process-status="processStatus">
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
          </el-steps>
        </el-col>
      </el-row>

      <!--  -->
      <el-row v-show="currentIndex === 0">
        <el-col>
          <el-form ref="jbxxFormRef" :model="jbxx.form.data" :rules="jbxx.form.rules" label-position="top">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="jbxx.form.data.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input type="number" min="0" v-model.number="jbxx.form.data.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input type="number" min="0" v-model.number="jbxx.form.data.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input type="number" min="0" v-model.number="jbxx.form.data.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="jbxx.form.data.goods_cat"
                :options="categories"
                :props="{
                  // 
                  label: 'cat_name',
                  // 
                  value: 'cat_id',
                }"
                clearable
                @change="onChangeOfCategories"
              ></el-cascader>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <!--  -->
      <el-row v-show="currentIndex === 1">
        <el-col>
          :
          <div>
            {{ spcs.list }}
          </div>

          <el-form label-position="top">
            <el-form-item v-for="v in spcs.list" :key="v.attr_id" :label="v.attr_name">
              <el-checkbox-group v-model="v._md_value">
                <el-checkbox v-for="v1 in v.attr_val_arr" :key="v1" :label="v1" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <!--  -->
      <el-row v-show="currentIndex === 2">
        <el-col>
          
          <div>
            {{ spsx.list }}
          </div>

          <el-form label-position="top">
            <el-form-item v-for="v in spsx.list" :key="v.attr_id" :label="v.attr_name">
              <el-input v-model="v._md_value"></el-input>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <!--  -->
      <el-row v-show="currentIndex === 3">
        <el-col>
          <!--  el-upload  axios axios  -->
          <!-- action: uploadURLaxiosbaseURL -->
          <!-- on-preview:  -->
          <!-- on-remove:  -->
          <!-- on-success:  -->
          <!-- file-list: : [{name: 'food.jpg', url: 'https://xxx.cdn.com/xxx.jpg'}] -->
          <!-- file-list:  -->
          <!-- list-type: picture:  -->
          <el-upload
            class="upload-demo"
            :action="uploadURL"
            :headers="uploadHeaders"
            :on-preview="onImgPreview"
            :on-remove="onImgRemove"
            :on-success="onImgSuccess"
            list-type="picture"
          >
            <template #default>
              <el-button size="small" type="primary"></el-button>
            </template>
            <template #tip>
              <div class="el-upload__tip">jpg/png500kb</div>
            </template>
          </el-upload>
        </el-col>
      </el-row>
      <!--  -->
      <el-row v-show="currentIndex === 4">
        <el-col>
          <quill-editor v-model="spnr.form.data.goods_introduce"></quill-editor>
        </el-col>
      </el-row>
      <!--  -->
      <el-row v-show="currentIndex === 5">
        <el-col>
          
        </el-col>
      </el-row>

      <el-row style="margin-top: 20px;">
        <el-col :span="12">
          <!--  -->
          <el-button @click="back" v-show="currentIndex === 0"></el-button>
          <!--  -->
          <el-button @click="prevStep" v-show="![0, 5].includes(currentIndex)"></el-button>
        </el-col>
        <el-col :span="12" style="text-align: right;">
          <el-button @click="nextStep" v-show="currentIndex !== 5">
            {{ currentIndex === 4 ? '' : '' }}
          </el-button>
          <el-button @click="back" v-show="currentIndex === 5"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog :title="previewImgDialog.title" :visible.sync="previewImgDialog.visible">
      <template #default>
        <img :src="previewImgDialog.imgURL" alt="" width="100%" />
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'GoodsAdd',
  data() {
    return {
      // 
      previewImgDialog: {
        title: '',
        visible: false,
        // url
        imgURL: '',
      },
      processStatus: 'process', // wait / process / finish / error / success
      currentIndex: 0,
      categories: [], // 
      /**  */
      jbxx: {
        form: {
          data: {
            goods_name: '',
            goods_price: '',
            goods_weight: '',
            goods_number: '',
            goods_cat: [],
          },
          rules: {
            goods_name: [{ required: true, message: '' }],
            goods_price: [{ required: true, message: '' }],
            goods_weight: [{ required: true, message: '' }],
            goods_number: [{ required: true, message: '' }],
            goods_cat: [
              { required: true, message: '' },
              {
                // 
                validator(rule, val, cb) {
                  if (!val || val.length !== 3) {
                    return cb(new Error(''));
                  }
                  cb();
                },
              },
            ],
          },
        },
      },
      /**  */
      spcs: {
        list: [], // 
        form: {
          data: {},
          rules: {},
        },
      },
      /**  */
      spsx: {
        list: [], // 
        form: {
          data: {},
          rules: {},
        },
      },
      /**  */
      sptp: {
        form: {
          data: {
            imgs: [],
          },
          rules: {},
        },
      },
      /**  */
      spnr: {
        form: {
          data: {
            goods_introduce: '',
          },
          rules: {},
        },
      },
    };
  },
  computed: {
    // id
    catId() {
      if (this.jbxx.form.data.goods_cat.length === 3) {
        return this.jbxx.form.data.goods_cat[this.jbxx.form.data.goods_cat.length - 1];
      }
      return null;
    },
    uploadURL() {
      return `${this.$request.defaults.baseURL}upload`;
    },
    uploadHeaders() {
      return {
        Authorization: sessionStorage.getItem('token'),
      };
    },
  },
  methods: {
    onImgSuccess(res, file, fileList) {
      console.log(res, file, fileList, '');
      if (res.meta.status === 200) {
        this.sptp.form.data.imgs.push({ pic: '/' + res.data.tmp_path });
      } else {
        this.$message.error(res.meta.msg);
      }
    },
    onImgRemove(file, fileList) {
      this.sptp.form.data.imgs = this.sptp.form.data.imgs.filter((v) => v.pic !== file.response.data.tmp_path);
    },
    onImgPreview(file) {
      console.log(file, '');
      this.previewImgDialog.imgURL = file.url;
      this.previewImgDialog.visible = true;
    },
    async loadManyOrOnlyList(sel) {
      // 
      const res = await this.$request.get(`categories/${this.catId}/attributes`, {
        params: {
          sel,
        },
      });
      if (res.data.meta.status === 200) {
        // 
        if (sel === 'many') {
          res.data.data.forEach((v) => {
            v._md_value = [];
            v.attr_val_arr = v.attr_vals === '' ? [] : v.attr_vals.split(' ');
          });
          this.spcs.list = res.data.data;
        }
        // 
        else if (sel === 'only') {
          res.data.data.forEach((v) => {
            v._md_value = v.attr_vals;
          });
          this.spsx.list = res.data.data;
        }
      } else {
        this.$message.error(res.data.meta.msg);
        // catch
        throw new Error(res.data.mate.msg);
      }
    },
    back() {
      this.$router.back();
    },
    // 
    async onChangeOfCategories() {},
    // 
    async loadCategories() {
      const res = await this.$request.get('categories');
      if (res.data.meta.status === 200) {
        this.categories = res.data.data;
      } else {
        this.$message.error(res.data.meta.msg);
      }
    },
    prevStep() {
      this.currentIndex--;
    },
    async nextStep() {
      // 
      if (this.currentIndex === 0) {
        try {
          await this.$refs.jbxxFormRef.validate();
          this.loadManyOrOnlyList('many');
        } catch (err) {
          // 
          console.log(err);
          return;
        }
      }
      // 
      else if (this.currentIndex === 1) {
        this.loadManyOrOnlyList('only');
      }
      // 
      else if (this.currentIndex === 2) {
      }
      // 
      else if (this.currentIndex === 3) {
      }
      // 
      else if (this.currentIndex === 4) {
        // 

        const spcsArr = this.spcs.list.map((v) => {
          return {
            attr_id: v.attr_id,
            attr_value: v._md_value.join(' '),
          };
        });
        // 
        // .filter(v => v.attr_value !== '');
        console.log(spcsArr, '');

        const spsxArr = this.spsx.list.map((v) => {
          return {
            attr_id: v.attr_id,
            attr_value: v._md_value,
          };
        });
        // 
        // .filter(v => v.attr_value !== '');
        console.log(spsxArr, '');

        console.log([...spcsArr, ...spsxArr], 'attrs');

        try {
          const res = await this.$request.post('goods', {
            goods_name: this.jbxx.form.data.goods_name, // 
            goods_cat: this.jbxx.form.data.goods_cat.join(','), // , ','
            goods_price: this.jbxx.form.data.goods_price, // 
            goods_number: this.jbxx.form.data.goods_number, // 
            goods_weight: this.jbxx.form.data.goods_weight, // 
            goods_introduce: this.spnr.form.data.goods_introduce, // 
            pics: this.sptp.form.data.imgs, // 
            attrs: [...spcsArr, ...spsxArr], //  ``  ``
          });
          if (res.data.meta.status !== 201) {
            // 
            this.$message.error(res.data.meta.msg);
            return;
          }
  
          // success
          this.processStatus = 'success';
        } catch (err) {
          this.$message.error('')
          console.log(err);
        }

      }
      // 
      else if (this.currentIndex === 5) {
      }
      this.currentIndex++;
    },
  },
  created() {
    console.log(this.$request, 'this.$request');

    this.loadCategories();
  },
};
</script>
